.hamburger {
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  position: relative;
  background-color: transparent;
  border: 0;
  font-size: 0px; }
  .hamburger span {
    display: block;
    position: absolute;
    top: 12px;
    left: 2px;
    right: 2px;
    height: 2px;
    background: #FFFFFF;
    opacity: 80%; }
    .hamburger span::after, .hamburger span::before {
      display: block;
      position: absolute;
      left: 0px;
      width: 100%;
      height: 2px;
      background: #FFFFFF;
      content: ""; }
    .hamburger span::after {
      top: -6px; }
    .hamburger span::before {
      top: 6px; }
  .hamburger:focus {
    outline: none; }
  @media (max-width: 725px) {
    .hamburger {
      display: block; } }

.hamburger-line span::before, .hamburger-line span::after {
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3, 0s; }

.hamburger-line span::before {
  transition-property: top, transform; }

.hamburger-line span::after {
  transition-property: bottom, transform; }

.hamburger-line.is-active span {
  background: none; }
  .hamburger-line.is-active span::before {
    top: 0;
    transform: rotate(-45deg); }
  .hamburger-line.is-active span::after {
    top: 0;
    transform: rotate(45deg); }
  .hamburger-line.is-active span::before, .hamburger-line.is-active span::after {
    transition-delay: 0s, 0.3s; }

